

function Informacion(props){
    
    
    return(
        <div>
            {props.data === "none" || props.data?.message === "none"?
            <div>
                <h2>No hay datos</h2>
            </div>
            :
            <div>
                <ul>
                    <li>Licencia :{props.data.licencia}</li>
                    <li>Matrcula :{props.data.matricula}</li>
                    <li>Nmero total de denuncias sin imagen :{props.data.totalDenSinImg}</li>
                    <li>Nmero total de denuncias con imagen :{props.data.totalDenConImg}</li>
                    <li>Informacin segura :{props.data.infoConfirmada?'SI':'NO'}</li>
                </ul>
                </div>}
        </div>
        
        
    )
}

export default Informacion